/* 教材选择 */
<template>
  <div class="category-filter">
    <div class="sub">
      <el-select
        v-model="stageId"
        placeholder="全部学段"
        popper-class="popper"
        style="width:100%;margin-bottom:20px"
      >
        <el-option value="-1" label="全部学段"></el-option>
        <el-option
          v-for="item in gradeLists"
          :key="item.code"
          :value="item.code"
          :label="item.text"
        ></el-option>
      </el-select>
      <el-select v-model="subjectId" popper-class="popper" style="width:100%;margin-bottom:20px">
        <el-option value="-1" label="全部科目"></el-option>
        <el-option v-for="item in subjectList" :key="item.id" :value="item.id" :label="item.name"></el-option>
      </el-select>
      <el-select v-model="gradeId" popper-class="popper" style="width:100%;margin-bottom:20px">
        <el-option value="-1" label="全部年级"></el-option>
        <el-option v-for="item in classLists" :key="item.id" :value="item.id" :label="item.text"></el-option>
      </el-select>
      <el-select v-model="versionId" popper-class="popper" style="width:100%">
        <el-option value="-1" label="全部版本"></el-option>
        <el-option v-for="item in versionList" :key="item.id" :value="item.id" :label="item.name"></el-option>
      </el-select>
    </div>
    <div class="select-lists">
      <el-scrollbar style="height:100%">
        <el-tree
          empty-text="请选择科目"
          :data="treeList"
          :props="defaultProps"
          @node-click="handleNodeClick"
        ></el-tree>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      /* 全部学段 */
      gradeLists: [],
      stageId: "-1",
      /* 全部科目 */
      subjectList: [],
      subjectId: "-1",
      /* 全部年级 */
      classLists: [],
      gradeId: "-1",
      /* 全部版本 */
      versionList: [],
      versionId: "-1",
      /* treeList */
      catalogId:"-1",
      treeList:[],
      defaultProps:{
        children: 'children',
        label: 'name'
      },
    }
  },
  created(){
    this.getSubjectTree();
    this.getAllGrade();
    this.getAllClass();
    this.getAllVersion();
  },
  watch:{
    listenChangeAll(){
      this.getSubjectTree();
    }
  },
  computed: {
    listenChangeAll() {
      const { subjectId, gradeId, versionId, stageId } = this;
      return { subjectId, gradeId, versionId, stageId };
    }
  },
  methods:{
    /* 获取科目树 */
    getSubjectTree() {
      let params = {
        periodId: this.stageId,
        subjectId: this.subjectId,
        textbookVersionId: this.versionId,
        gradeId:this.gradeId
      };
      this.$api.getSubjectTree(params).then(res => {
        this.treeList = res.data;
      });
    },
    /* 全部学段 */
    getAllGrade() {
      const params = { type: "stage" };
      this.$api.dictGetByType(params).then(res => {
        this.gradeLists = res.data;
      });
    },

    /* 全部科目 接口*/
    getAllSubject(){
      // 不传 获取 全部科目 传 学段periodId 获取对应的
      this.$api.getSubjectByGrade().then(res => {
        this.subjectList = res.data;
      });
    },
    /* 全部年级 */
    getAllClass() {
      const params = { type: "grade" };
      this.$api.dictGetByType(params).then(res => {
        this.classLists = res.data;
      });
    },
    /* 全部版本 */
    getAllVersion() {
      this.$api.getKeyAndValue().then(res => {
        this.versionList = res.data;
      });
    },
    handleNodeClick(data) {
      if(!data.hasNext) {
        this.catalogId = data.id;
        this.$emit("filter-category",{
          catalogId:this.catalogId,
          stageId:this.stageId,
          versionId:this.versionId,
          subjectId:this.subjectId,
          gradeId:this.gradeId
        })
      }
    }
  }
};
</script>

<style scoped>
.category-filter {
  height: calc(100% - 50px);
}
.select-lists {
  margin-top: 16px;
  height: calc(100% - 220px);
}
</style>
<style>
.select-lists .el-tree {background: #fff;}
</style>